.mc__navbar {
    background-color: rgb(66, 65, 65);
    display: flex;
    align-items: center;
}

.mc__navbar-links {
    float: left;
    display: flex;
    justify-content: left;
    position: fixed;
    top: 0;
    width: 100%;
    list-style-type: none;
    margin: 0;
    overflow: hidden;
    background-color: rgb(66, 65, 65);
}

.mc__navbar-links a {
    display: block;
    color: white;
    text-align: center;
    padding: 1.5rem 4rem;
    text-decoration: none;
}

.mc__navbar-links_container a:hover {
    background-color: #111;
}

.mc__navbar-links_logo {
    padding-top: 1.8rem; 
    padding-left: 4rem; 
    padding-right: 4rem;
}

.mc__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.mc__navbar-links_container p,
.mc_navbar-menu_container p {
    color: white;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 0rem;
    cursor: pointer;

}

.mc__navbar-links_discord {
    margin-left: auto;
    margin-right: 0rem;


}

.mc__navbar-links_discord img {
    width: 30px;
    height: 21px;

}

.mc__navbar-menu {
    margin-left: auto;
    margin-top: 1.5rem;
    margin-right: 1.5rem;
    margin-bottom: 1.5rem;
    display: none;
    position: relative;

}

.mc__navbar-menu svg {
    cursor: pointer;
}

.mc__navbar-menu_container {
    display: flex;
    color: white;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: rgb(88, 88, 88);
    padding: 1rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 0.7rem;
    margin-right: -2rem;
    min-width: 150px;
    border-bottom-left-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}

.mc__navbar-menu_container p {
    margin: 1rem 1rem;
    font-family: var(--font-family);
}

.mc__navbar-menu_discord {
    margin-right: 1rem;
    margin-top: 0.5rem;
}

.mc__navbar-menu_discord img {
    width: 30px;
    height: 21px;
}

@media screen and (max-width: 1300px) {
    .mc__navbar-links_container {
        display: none;
    }

    .mc__navbar-menu {
        display: flex;   
    }

    .mc__navbar-menu_discord {
        display: none;   
    }

    .mc__navbar-links_discord {
        margin-top: 0.2rem; 
        margin-right: 1rem;  
    }
}

@media screen and (max-width: 470px) {
    .mc__navbar-links_discord {
        margin-top: 0.2rem; 
        margin-right: 1rem;  
    }

    .mc__navbar-links_logo {
        padding-top: 1.8rem; 
        padding-left: 4rem; 
        padding-right: 0rem;
    }
}

@media screen and (max-width: 380px) {
    .mc__navbar-links_logo {
        padding-left: 3rem; 
    }

    .mc__navbar-menu_discord {
        display: flex;   
    }

    .mc__navbar-links_discord {
        display: none;
    }
}

@media screen and (max-width: 280px) {
    .mc__navbar-links_logo {
        padding-left: 2rem; 
        padding-top: 1.5rem;
        padding-bottom: 1rem;
    }

    .mc__navbar-menu {
        margin-top: 1.2rem;
        margin-right: 1.5rem;
        margin-bottom: 1rem;
    }
    .mc__navbar-menu_container {
        margin-top: 0.2rem;
        min-width: 100px;
    }

}